footer{
    background: var(--color-primary);
    padding: 40px 0 ;
    text-align: center;
    font-size: 14px;
    margin-top: 100px;
}
footer a{
    color: var(--color-bg);
}
.footer_logo{
    font-size: 25px;
    font-weight: 500;
    margin-bottom: 30px;
    display: inline-block;

}
.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
    margin:0 auto 30px;
}
.footer_socials{
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-bottom: 40px;

}
.footer_socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 12px;
    border-radius: 10px;
    display: flex;
    border: 1px solid transparent;
    transition: var(--transition);
}
.footer_socials a:hover{
    background: transparent;
    border-color: var(--color-bg);
    color: var(--color-bg);
}
.footer_copyrigth{
    margin-bottom: 4rem;
    color: var(--color-bg);

}
.footer_copyrigth a{
    color: var(--color-white);
}


/* responsive */
@media (max-width: 600px) {
      
    
    .permalinks{
        flex-direction: column;
        gap: 20px;
    }
    
}